@import './../mfv'   

div.service-cards
    @include flex(center, row)
    flex-wrap: wrap
    padding-bottom: 7rem
    div.service-card
        flex-basis: 95%
        margin: 1rem .5rem
        height: 35vh
        @media screen and ( min-width: 700px )
            flex-basis: 45%

div.underline-header
    width: 45%
    margin-left: 1rem
    position: relative
    @include bottom-border(darkgray)
    margin-bottom: 2rem
    box-shadow: 0px 1px 0px 0px gray
    @media screen and ( min-width: 700px )
        width: 35%
    h1.header
        font-size: 3rem
   


img.card-img
    height: 25vh
    width: 100%
img.MuiCardMedia-img
    object-fit: fill !important
div.MuiCardContent-root
    height: 6vh
    display: flex
    flex-wrap: wrap
    padding: 0 1.5rem
    h2
        padding-top: .3rem
        flex-basis: 100%
        font-size: 1.7rem
    p
        flex-basis: 50%
        font-size: 1.5rem
div.MuiCardActions-root
    justify-content: center
    height: 4vh
    padding: 0
    width: 100%
    background-color: $flower
button.MuiButtonBase-root
    padding: 0
    text-transform: unset
    width: 100%
    span.MuiButton-label
        font-size: 1.5rem
        color: $white
        