@import './../mfv'  

div.page-cards
    @include wrap(row)
    
    
div.page-card
    @include flex(center, column)
    @include light-border(gray)   
    flex-basis: 100%
    height: 25vh
    justify-content: space-evenly
    padding: 1.5rem 0
    @media screen and ( min-width: 700px )
        flex-basis: 50%
    h1.card-header
        width: 100%
        font-size: 2.5rem
        padding-left: 5%
        font-style: italic
        @media screen and ( min-width: 700px )
            font-size: 2.7rem
    h3.card-desc
        font-size: 1.3rem
        width: 75%
        padding-left: 10%
        padding: 2% 0
        overflow: hidden
        @media screen and ( min-width: 425px )
            font-size: 1.7rem
        @media screen and ( min-width: 700px )
            width: 65%
            font-size: 2rem
    a
        padding-left: 65%
    svg.card-arrow
        font-size: 2.5rem !important
        